<template>
        <div class="modal-card" style="width: auto">
            <header class="modal-card-head">
                <p class="modal-card-title">Download from Search</p>
            </header>
            <section class="modal-card-body">
              
              <b-field label="Filename">
                <b-input
                  v-model="filename"
                  placeholder="Downloaded file name"
                  required>
                </b-input>
              </b-field>

            </section>
            <footer class="modal-card-foot">
                <button class="button" type="button" @click="$parent.close()">Close</button>
                <a class="button is-primary" @click="download">Download</a>
            </footer>
        </div>
    </template>

<script>
var request = require('request')
/* eslint-disable */
export default {
props: ['hashvalue'],
    data () {
        return {
            hashvalue: "",
            filename: "",
        }
    },
    methods: {
        download: function(e) {
            e
            if (this.filename != "" && this.hashvalue != "") {
                let form = {'Peer': '',
                            'Filename': this.filename,
                            'HashValue': this.hashvalue,
                           }
                request.post({
                    headers: {'content-type' : 'application/json'},
                    url:     'http://127.0.0.1:8080/download',
                    body:    JSON.stringify(form)
                }, function(error, response, body){
                });
                this.$parent.close()
            }
        },
    }
  }
</script>

